const getInitialPosts = (): BlogPost[] => {
  if (typeof window === "undefined") return defaultPosts

  const stored = localStorage.getItem("shambaConnectBlogPosts")
  if (stored) {
    try {
      return JSON.parse(stored)
    } catch {
      return defaultPosts
    }
  }

  // First time - save defaults to localStorage
  localStorage.setItem("shambaConnectBlogPosts", JSON.stringify(defaultPosts))
  return defaultPosts
}

interface BlogStore {
  posts: BlogPost[]
  addPost: (post: Omit<BlogPost, "id" | "views">) => void
  updatePost: (id: string, updates: Partial<BlogPost>) => void
  deletePost: (id: string) => void
  getPostBySlug: (slug: string) => BlogPost | undefined
  incrementViews: (slug: string) => void
}

export const useBlogStore = create<BlogStore>((set, get) => ({
  posts: getInitialPosts(),

  addPost: (post) => {
    const newPost: BlogPost = {
      ...post,
      id: Date.now().toString(),
      views: 0,
    }
    set((state) => {
      const newPosts = [newPost, ...state.posts]
      if (typeof window !== "undefined") {
        localStorage.setItem("shambaConnectBlogPosts", JSON.stringify(newPosts))
      }
      return { posts: newPosts }
    })
  },

  updatePost: (id, updates) => {
    set((state) => {
      const newPosts = state.posts.map((post) => (post.id === id ? { ...post, ...updates } : post))
      if (typeof window !== "undefined") {
        localStorage.setItem("shambaConnectBlogPosts", JSON.stringify(newPosts))
      }
      return { posts: newPosts }
    })
  },

  deletePost: (id) => {
    set((state) => {
      const newPosts = state.posts.filter((post) => post.id !== id)
      if (typeof window !== "undefined") {
        localStorage.setItem("shambaConnectBlogPosts", JSON.stringify(newPosts))
      }
      return { posts: newPosts }
    })
  },

  getPostBySlug: (slug) => {
    return get().posts.find((post) => post.slug === slug)
  },

  incrementViews: (slug) => {
    set((state) => {
      const newPosts = state.posts.map((post) => (post.slug === slug ? { ...post, views: post.views + 1 } : post))
      if (typeof window !== "undefined") {
        localStorage.setItem("shambaConnectBlogPosts", JSON.stringify(newPosts))
      }
      return { posts: newPosts }
    })
  },
}))

export function generateSlug(title: string): string {
  return title
    .toLowerCase()
    .replace(/[^a-z0-9]+/g, "-")
    .replace(/^-|-$/g, "")
}

export function saveBlogPost(post: Omit<BlogPost, "id" | "views">): BlogPost {
  const store = useBlogStore.getState()
  store.addPost(post)

  // Zustand updates are async; read the latest state after the write.
  // The new post is added to the beginning of the array.
  return useBlogStore.getState().posts[0]
}

export function updateBlogPost(id: string, updates: Partial<BlogPost>): void {
  const store = useBlogStore.getState()
  store.updatePost(id, updates)
}

export function getBlogPostBySlug(slug: string): BlogPost | undefined {
  const store = useBlogStore.getState()
  return store.getPostBySlug(slug)
}
